<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Withdrawal Settings</p>
        </div>
        <div class="orderDetailWrap">
			<div class="WithdrawalSettingNotice" v-if="editable">
                For the safety of your assets, the modification interval cannot be less than 15 days.<br>Please confirm carefully before submitting.
			</div>
			<div class="WithdrawalSettingNotice" v-else>
				For the safety of your assets, the modification interval cannot be less than 15 days.<br>Last modified: {{lastModified}}
			</div>
			<div class="inputItem" style="margin-bottom: .52rem;">
				<div class="inputTop flexBetween">
					<span class="inputLabel">Real name</span>
				</div>
				<div class="inputOuter flexBetween" :class='inputActive == "input2" ? "greenBorder":""'>
					<input class='normalInput' :disabled='!editable' type="text" v-model="withdrawalData.realName"  maxlength="30" @focus="focusInput('input2')" @input="blurWithdrawalName(withdrawalData.realName)">
					<img class='inputClearIcon' v-if="withdrawalData.realName && editable" @click="withdrawalData.realName = '';blurWithdrawalName(withdrawalData.realName)" src="../assets/images/clearInputIcon.png" alt="">
                    <p class='errorMsgText' v-if="showNameAlert">{{withdrawalAlertText}}</p>
				</div>
			</div>
			<div class="inputItem" style="margin-bottom: .52rem;" v-if="withdrawalData.bankList && withdrawalData.bankList.length > 0">
				<div class="inputTop flexBetween">
					<span class="inputLabel">Bank</span>
				</div>
				<div class="inputOuter flexBetween" :class='inputActive == "input3" ? "greenBorder":""' @click="popupBox = true">
					<div class='normalInput'>{{selectedBankData.name}}</div>
					<img class='settingRightArrow' src="../assets/images/relultArrow.png" alt="">
				</div>
			</div>
			<div class="inputItem" style="margin-bottom: .52rem;" v-else>
				<div class="inputTop flexBetween">
					<span class="inputLabel">IFSC Code</span>
				</div>
				<div class="inputOuter flexBetween" :class='inputActive == "input4" ? "greenBorder":""'>
					<input class='normalInput'  :disabled='!editable' type="text" v-model="withdrawalData.ifscCode" @focus="focusInput('input4')" @input="blurWithdrawalIFSC(withdrawalData.ifscCode)">
					<img class='inputClearIcon' v-if="withdrawalData.ifscCode && editable" @click="withdrawalData.ifscCode = '';blurWithdrawalIFSC(withdrawalData.ifscCode)" src="../assets/images/clearInputIcon.png" alt="">
                    <p class='errorMsgText' v-if="showIFSCAlert" >{{IFSCAlertText}}</p>
				</div>
			</div>
			<div class="inputItem" style="margin-bottom: .52rem;">
				<div class="inputTop flexBetween">
					<span class="inputLabel">Account Number</span>
				</div>
				<div class="inputOuter flexBetween" :class='inputActive == "input5" ? "greenBorder":""'>
					<input class='normalInput'  :disabled='!editable' v-model="withdrawalData.account" maxlength="19" type="number"  @focus="focusInput('input5')" @input="blurWithdrawalAccount(withdrawalData.account)">
					<img class='inputClearIcon' v-if="withdrawalData.account && editable" @click="withdrawalData.account = '';blurWithdrawalAccount(withdrawalData.account)" src="../assets/images/clearInputIcon.png" alt="">
                    <p class='errorMsgText' v-if="showAccountAlert">{{AccountAlertText}}</p>
				</div>
			</div>
			<div class="inputItem" style="margin-bottom: .52rem;">
				<div class="inputTop flexBetween">
					<span class="inputLabel">Mobile Number</span>
				</div>
				<div class="inputOuter flexBetween" :class='inputActive == "input6" ? "greenBorder":""'>
					<input class='normalInput'  :disabled='!editable' v-model="withdrawalData.cellphone" maxlength="13" type="number" @focus="focusInput('input6')" @input="blurWithdrawalPhone(withdrawalData.cellphone)">
					<img class='inputClearIcon' v-if="withdrawalData.cellphone && editable" @click="withdrawalData.cellphone = '';blurWithdrawalPhone(withdrawalData.cellphone)" src="../assets/images/clearInputIcon.png" alt="">
                    <p class='errorMsgText' v-if="showPhoneAlert">{{PhoneAlertText}}</p>
				</div>
			</div>
			<div class="inputItem" style="margin-bottom: .52rem;">
				<div class="inputTop flexBetween">
					<span class="inputLabel">Email Address</span>
				</div>
				<div class="inputOuter flexBetween" :class='inputActive == "input7" ? "greenBorder":""'>
					<input class='normalInput' :disabled='!editable' v-model="withdrawalData.email" type="email" maxlength="35" @focus="focusInput('input7')" @input="blurWithdrawalEmail(withdrawalData.email)">
					<img class='inputClearIcon' v-if="withdrawalData.email && editable" @click="withdrawalData.email = '';blurWithdrawalEmail(withdrawalData.email)" src="../assets/images/clearInputIcon.png" alt="">
                    <p class='errorMsgText' v-if="showEmailAlert">{{EmailAlertText}}</p>
				</div>
			</div>
            <div class="submitBtn " :class="allCheck && editable  ? 'GradientAllowBtn':''" style="margin-top: .73rem;" @click="Continue()">Save</div>
		</div>
		<van-popup v-model="popupBox" position='right'>
			<div class="bankBox flexCenterColumn" v-if="withdrawalData && withdrawalData.bankList && withdrawalData.bankList.length > 0">
				<div class="bankItem" :class="item.code == selectedBankData.code ? 'bankActive':''" v-for="(item,index) in withdrawalData.bankList" :key="index" @click="selectBank(item)">{{item.name}}</div>
			</div>
		</van-popup>
        <van-dialog class='alertModelBox' v-model="alertModelBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Unable to modify
                    <img class='alertModelCloseIcon' @click="alertModelBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">It’s less than 15 days since the last modification. In order to protect your account balance, the withdrawal account cannot be modified now.<br>Please contact live assistant for details.</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 blueAllowBtn" style="width: 100%;" @click="alertModelBox = false">OK</div>
                    </div>
                </div>
            </template>
        </van-dialog>
    </div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'WithdrawalSettings',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                
                popupBox: false,
				inputActive: '',

				realName: '',
				withdrawalData: {
                },
				selectedBankData: {},

				showNameAlert: false,
				withdrawalAlertText: '',
				realNameCheck: true,

				showIFSCAlert: false,
				IFSCAlertText: '',
				IFSCcheck: true,

				showAccountAlert:false,
				AccountAlertText: '',
				accountCheck: true,

				showPhoneAlert: false,
				PhoneAlertText: '',
				phoneCheck: true,

				showEmailAlert: false,
				EmailAlertText: '',
				emailCheck: true,

				allCheck: false,

                editable: true,
                alertModelBox: false,
                lastModified: '',
			}
		},
		created(){
			this.getData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
            getData: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/pay/withdraw/status').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        if(res.data.normal && res.data.normal.settings && res.data.normal.settings.length > 0) {
                            if(res.data.normal.settings[0].type == 'IFSC') {
                                this.withdrawalData = res.data.normal.settings[0].IFSC
                                this.editable = res.data.normal.settings[0].editable
                            }
                        }
						// if(this.withdrawalData.bankList && this.withdrawalData.bankList.length > 0) {
						// 	for (let index = 0; index < this.withdrawalData.bankList.length; index++) {
						// 		if(this.withdrawalData.bank == this.withdrawalData.bankList[index].code) {
						// 			this.selectedBankData = this.withdrawalData.bankList[index]
						// 		}
						// 	}
						// }
						this.checkAll()
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
			Continue: function(){
                if(!this.editable) {
                    this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    return
                }
				if(!this.allCheck) {
					return
				}
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Unable to modify',
                            forbidClick: true,
                            duration: 0,
                            className:'smallToast'
                        });
                    }
                }, 500);
				let params = this.withdrawalData
				if(params.bank) {
					params.bank = this.selectedBankData.code
				}
				https.fetchPost('/api/pay/withdraw/setting/ifsc',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.$toast({
							message: 'Successfully modified',
							forbidClick: true,
							icon: require('../assets/images/successIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						setTimeout(() => {
							this.goBack()
						}, 2000);
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.Continue()
					}
				})
			},
			selectBank(item){
				this.selectedBankData= item;
				this.withdrawalData.bank = item.name
				this.popupBox = false;
				this.checkAll()
			},	
			// blur
			blurWithdrawalName(name){
				let rge = /[<'>%*`?/&$]/g;
				if(rge.test(name)){
					this.showNameAlert = true
					this.withdrawalAlertText = 'Invalid real name'
					this.realNameCheck = false
				}else if(!name || name.length < 2) {
					this.showNameAlert = true
					this.withdrawalAlertText = 'At least 2 characters'
					this.realNameCheck = false
				}else {
					this.showNameAlert = false
					this.withdrawalAlertText = ''
					this.realNameCheck = true
				}
				this.checkAll()
			},
			blurWithdrawalIFSC(withdrawalIFSC){
                console.log(1)
				let rge = /^[A-Za-z]{4}0[A-Za-z0-9]{6}$/;
				
				if(!withdrawalIFSC || withdrawalIFSC.length != 11) {
					this.showIFSCAlert = true
					this.IFSCAlertText = 'Must be 11 characters'
					this.IFSCcheck = false
				}else if(!rge.test(withdrawalIFSC)){
					this.showIFSCAlert = true
					this.IFSCAlertText = 'Invalid IFSC code'
					this.IFSCcheck = false
				}else {
					this.showIFSCAlert = false
					this.IFSCAlertText = ''
					this.IFSCcheck = true
				}
				this.checkAll()
			},
			blurWithdrawalAccount(withdrawalAccount) {
				let rge = /^[0-9]*$/;
				if(!withdrawalAccount || withdrawalAccount.length < 9) {
					this.showAccountAlert = true
					this.AccountAlertText = 'At least 9 characters'
					this.accountCheck = false
				}else if(!rge.test(withdrawalAccount)){
					this.showAccountAlert = true
					this.AccountAlertText = 'Invalid Account number'
					this.accountCheck = false
				}else {
					this.showAccountAlert = false
					this.AccountAlertText = ''
					this.accountCheck = true
				}
				this.checkAll()
			},
			blurWithdrawalPhone(withdrawalPhone) {
				let rge = /^[0-9]*$/;
				if(!withdrawalPhone || withdrawalPhone.length < 6) {
					this.showPhoneAlert = true
					this.PhoneAlertText = 'At least 6 characters'
					this.phoneCheck = false
				}else if(!rge.test(withdrawalPhone)){
					this.showPhoneAlert = true
					this.PhoneAlertText = 'Invalid mobile number'
					this.phoneCheck = false
				}else {
					this.showPhoneAlert = false
					this.PhoneAlertText = ''
					this.phoneCheck = true
				}
				this.checkAll()
			},
			blurWithdrawalEmail(withdrawalEmail) {
				let rge = /^[a-zA-Z0-9@._-]+$/g;
				if(!withdrawalEmail || withdrawalEmail.length < 5) {
					this.showEmailAlert = true
					this.EmailAlertText = 'At least 5 characters'
					this.emailCheck = false
				}else if(!rge.test(withdrawalEmail)){
					this.showEmailAlert = true
					this.EmailAlertText = 'Invalid email address'
					this.emailCheck = false
				}else {
					this.showEmailAlert = false
					this.EmailAlertText = ''
					this.emailCheck = true
				}
				this.checkAll()
			},
			checkAll(){
				if((!this.withdrawalData.realName || !this.realNameCheck) || (!this.withdrawalData.account || !this.accountCheck) || (!this.withdrawalData.cellphone || !this.phoneCheck) || (!this.withdrawalData.email || !this.emailCheck)) {
					console.log(this.realNameCheck)
					console.log(this.accountCheck)
					console.log(this.phoneCheck)
					console.log(this.emailCheck)
					this.allCheck = false
					return
				}else {
					if(this.withdrawalData.bankList) {
						if(!this.withdrawalData.bank) {
							this.allCheck = false
					console.log(2)
							return
						}else {
							this.allCheck = true
					console.log(3)
						}
					}else {
						if(!this.withdrawalData.ifscCode || !this.IFSCcheck) {
							this.allCheck = false
					console.log(4)
							return
						}else {
							this.allCheck = true
					console.log(5)
						}
					}
				}
			},
			focusInput(inputName){
				this.inputActive = inputName
			},
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
<style scoped>
    .pageWrap {
        background: #fff;
    }
	.orderDetailWrap {
        padding: .35rem .45rem;
        background: #fff;
	}
    .inputLabel {
        margin-bottom: .09rem;
        font-size: .27rem;
        color: #687380;
        line-height: .33rem;
    }
    .inputOuter {
        position: relative;
        height: .82rem;
        padding: 0 .35rem;
        border-radius: .41rem;
        border:.02rem solid #CED8E4;
    }
    .normalInput {
        
        font-size: .29rem;
        font-weight: 500;
        color: #0F2332;
        line-height: .35rem;
        height: .82rem;
        flex-grow: 1;
    }
    .inputClearIcon {
        display: flex;
        width: .31rem;
        height: .31rem;
    }
    .errorMsgText {
        position: absolute;
        bottom: -.34rem;
        right: 0;
        text-align: right;
        font-size: .24rem;
        color: #FF274C;
        line-height: .28rem;
    }

    .bankBox {
		min-height: 100%;
		padding: .96rem .48rem;
		box-sizing: border-box;
		align-items: flex-start;
	}
	.bankItem {
		margin-bottom: .44rem;
		font-size: .28rem;
		font-weight: bold;
		color: #1C4140;
		line-height: .40rem;
	}
	.bankActive {
		font-size: .28rem;
		font-weight: bold;
		color: #0DC253;
		line-height: .40rem;
	}
	.WithdrawalSettingNotice {
		margin-bottom: .48rem;
		font-size: .24rem;
		color: #7B9695;
		line-height: .32rem;
		letter-spacing: .01rem;
	}
	.settingRightArrow {
		width: .32rem;
		height: .32rem;
	}


	.orderDetailWrap {
		padding: .48rem .44rem;
	}
	.withdrawalAccountSetting {
		background: #fff;
	}
</style>
<style>
	.withdrawalAccountSetting .van-popup {
		height: 100%;
		width: 6rem;
	}
    input:disabled {
        background: #fff;
    }
